import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_RENDERED, COMPONENT_DISCONNECTED} = actionTypes;
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-stepper.scss';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-tooltip';
import {
	schedule,
	unschedule,
	createTooltipActionHandler
} from '@servicenow/now-tooltip';
import {t} from 'sn-translate';
import focusBehavior from '@servicenow/behavior-focus';
import resizeBehavior from '@servicenow/behavior-resize';
import {
	getShadowRootHost,
	measureTextFactory,
	getActiveElement
} from '@servicenow/library-enhanced-utils';
import inRange from 'lodash/inRange';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const RIGHT_ARROW = Symbol('right-arrow');
const LEFT_ARROW = Symbol('left-arrow');

// CALCULATE WIDTH THINGS
const mt = measureTextFactory('16px Source Sans Pro,sans-serif');
const mst = measureTextFactory('12px Source Sans Pro,sans-serif');

const measureText = (text) => {
	return text ? Math.ceil(mt(text)) : 0;
};
const measureSmallText = (text) => {
	return text ? Math.ceil(mst(text)) : 0;
};

const MIN_STEP_WIDTH = 42;
const MAX_STEP_WIDTH = 160;
const PAGINATION_ARROWS_AND_PADDING = 72;
const MIN_DISTANCE_BETWEEN_STEPS = 16;

const getLabelWidth = (item) => {
	return item.icon ? measureText(item.label) + 18 : measureText(item.label);
};

const getSublabelWidth = (item) => {
	// `subLabel` has been deprecated in favor of `sublabel`
	return measureSmallText(item.subLabel || item.sublabel);
};

const getLongestStepWidth = (item) => {
	return Math.max(getLabelWidth(item), getSublabelWidth(item));
};

const minStepperWidth = (items) => {
	return getStepWidth(items) + 80;
};

const getStepWidth = (items) => {
	return Math.min(
		MAX_STEP_WIDTH,
		Math.max(
			MIN_STEP_WIDTH,
			items.reduce((acc, item) => Math.max(acc, getLongestStepWidth(item)), 0)
		)
	);
};

/**
 * Returns the width of the stepper container, updating as the window is resized. If the
 * window is resized to smaller than the min width of the container, returns
 * minWidth. If the window dimensions are undefined, returns 'unknown'.
 */
const getContainerWidth = (width, minWidth) => {
	if (typeof width === 'number') {
		return Math.max(width, minWidth);
	}
	return minWidth;
};

export const getStepsPerPage = (items, width) => {
	const stepWidth = getStepWidth(items);
	const containerWidth = getContainerWidth(width, minStepperWidth(items));
	const availWidth = containerWidth - PAGINATION_ARROWS_AND_PADDING; // subtract the width of the pagination arrows and their outer padding
	const stepsPerPage = Math.floor(
		availWidth / (stepWidth + MIN_DISTANCE_BETWEEN_STEPS)
	);
	if (stepsPerPage < 1) {
		return 1; // we should always show at least one step
	}
	return stepsPerPage > items.length ? items.length : stepsPerPage;
};

const showTooltip = (updateState, stepper, label) => {
	if (stepper.scrollWidth > stepper.clientWidth) {
		updateState({
			currentTooltip: {
				ref: stepper,
				content: label,
				opened: true
			}
		});
	}
};

const hideTooltip = (updateState) => {
	updateState({
		currentTooltip: {ref: null, content: '', opened: false}
	});
};

// RENDERING STUFF

const getCompletedCount = (items) =>
	items.filter((item) => item.progress === 'done').length;

const renderCompletedCount = (total, completed) => (
	<span
		className="now-line-height-crop"
		aria-label={t('{0} of {1} steps completed', completed, total)}>
		<span className="now-stepper-completed-count" aria-hidden="true">
			{t('{0}/{1} Completed', completed, total)}
		</span>
	</span>
);

const renderStepProgressIndicator = (progress, isSelected, stepNumber) => {
	if (progress === 'done' && !isSelected) {
		return (
			<div class={{'now-stepper-step-progress': true}}>
				<now-icon icon="check-circle-fill" size="lg" />
			</div>
		);
	}
	const modifierClassName = isSelected ? '-active' : '-' + progress;
	return (
		<span
			class={{
				'now-stepper-step-progress': true
			}}>
			<span
				class={{
					'now-stepper-step-progress-label': true,
					[modifierClassName]: true
				}}>
				<span className="now-line-height-crop" aria-hidden="true">
					{typeof stepNumber === 'number' ? stepNumber : ''}
				</span>
			</span>
		</span>
	);
};

const getProgressLabel = (progress, isSelected) => {
	if (isSelected) {
		return t('Status: Active.');
	}
	if (progress === 'none') {
		return t('Status: Not started.');
	}
	if (progress === 'partial') {
		return t('Status: In progress.');
	}
	if (progress === 'done') {
		return t('Status: Complete.');
	}
};

// eslint-disable-next-line no-unused-vars
const renderStep = ({
	key,
	item,
	stepNumber,
	hideStartConnectorLine,
	hideEndConnectorLine,
	isSelected,
	isTabbable,
	onSelect,
	onFocus,
	showTooltip,
	hideTooltip,
	updateState,
	componentId
}) => {
	const {label, icon, progress = 'none', disabled} = item;

	// `subLabel` has been deprecated in favor of `sublabel`
	const sublabel = item.subLabel || item.sublabel;
	const progressLabel = getProgressLabel(progress, isSelected);

	const handleMouseLeave = () => {
		schedule(componentId, () => hideTooltip(updateState));
	};

	return (
		<div key={key} className="now-stepper-step" role="presentation">
			<hr
				role="presentation"
				class={{
					'now-stepper-connector': true,
					'-start': true,
					'-is-first-step': hideStartConnectorLine
				}}
			/>
			<button
				role="tab"
				tabIndex={isTabbable ? undefined : '-1'}
				aria-selected={isSelected ? 'true' : 'false'}
				class={{
					'now-stepper-step-button': true,
					'-active': isSelected,
					['-' + progress]: progress && !isSelected,
					'is-disabled': disabled
				}}
				on-click={() => {
					if (!disabled) {
						onSelect(item);
					}
				}}
				on-focus={() => onFocus(item)}
				data-itemid={item.id}>
				{renderStepProgressIndicator(progress, isSelected, stepNumber)}
				<span className="now-a11y-label">
					{stepNumber ? t('Step {0}.', stepNumber) : undefined}
				</span>
				<span className="now-align -block-center -column">
					<span className="now-stepper-step-label-parent">
						{icon ? (
							<now-icon className="now-m-inline-end--xxs" icon={icon} />
						) : (
							undefined
						)}
						{label ? (
							<span className="now-line-height-crop">
								<span
									className="now-stepper-step-label"
									on-mouseenter={(e) => {
										const stepper = e.currentTarget;
										schedule(componentId, () =>
											showTooltip(updateState, stepper, item.label)
										);
									}}
									on-mouseleave={handleMouseLeave}
									aria-label={`${label}.`}>
									{label}
								</span>
							</span>
						) : (
							undefined
						)}
					</span>
					{sublabel ? (
						<span className="now-stepper-step-sublabel-parent">
							<span className="now-stepper-step-sublabel-line-height-crop">
								<span
									className="now-stepper-step-sublabel"
									on-mouseenter={(e) => {
										const stepper = e.currentTarget;
										schedule(componentId, () =>
											showTooltip(updateState, stepper, sublabel)
										);
									}}
									on-mouseleave={handleMouseLeave}
									aria-label={`${sublabel}.`}>
									{sublabel}
								</span>
							</span>
						</span>
					) : (
						undefined
					)}
				</span>
				<span className="now-a11y-label">{progressLabel}</span>
				<span className="now-a11y-label">{disabled ? t('Disabled.') : ''}</span>
			</button>
			<hr
				role="presentation"
				class={{
					'now-stepper-connector': true,
					'-end': true,
					'-is-last-step': hideEndConnectorLine
				}}
			/>
		</div>
	);
};

const renderSteps = ({
	items,
	rangeStart,
	rangeEnd,
	selectedItem,
	focusedItem,
	lastFocusedItem,
	hideNumbers,
	isPaginated,
	dispatch,
	updateState,
	componentId
}) => {
	const visibleItems = items.slice(rangeStart, rangeEnd);
	const tabbableItem = getTabbableItem(
		items,
		rangeStart,
		rangeEnd,
		focusedItem,
		lastFocusedItem
	);
	const onSelect = (item) =>
		dispatch(() => {
			return {
				type: 'NOW_STEPPER#SELECTED_ITEM_SET',
				payload: {value: item.id, item}
			};
		});

	const onFocus = async (item) => {
		await Promise.resolve();
		updateState({focusedItem: item, lastFocusedItem: item});
	};

	return visibleItems.map((item, index) => {
		const indexInAllItems = rangeStart + index;
		const isFirstStep = indexInAllItems === 0;
		const isLastStep = indexInAllItems === items.length - 1;
		return renderStep({
			key: index,
			item,
			stepNumber: hideNumbers ? null : indexInAllItems + 1,
			hideStartConnectorLine: isFirstStep && !isPaginated,
			hideEndConnectorLine: isLastStep && !isPaginated,
			isSelected: selectedItem === item.id,
			isTabbable: tabbableItem === item,
			onSelect,
			onFocus,
			showTooltip,
			hideTooltip,
			updateState,
			componentId
		});
	});
};

const getRange = (anchor, offset, stepsPerPage, length) => {
	if (anchor === 'start') {
		const start = offset;
		const end = offset + stepsPerPage;
		return [start, end];
	}
	if (anchor === 'end') {
		const start = length - offset - stepsPerPage;
		const end = length - offset;
		return [start, end];
	}
};

const getTabbableItem = (
	items,
	rangeStart,
	rangeEnd,
	focusedItem,
	lastFocusedItem
) => {
	if (lastFocusedItem) {
		return lastFocusedItem;
	}
	if (!focusedItem || focusedItem === LEFT_ARROW) {
		return items[rangeStart];
	}
	if (focusedItem === RIGHT_ARROW) {
		return items[rangeEnd - 1];
	}
	return null;
};

const renderArrow = ({icon, a11yLabel, disabled, onClick, onFocus}) => (
	<now-button-iconic
		config-aria={{'aria-label': a11yLabel}}
		className="now-stepper-pagination-button"
		size="lg"
		variant="tertiary"
		icon={icon}
		disabled={disabled}
		bare
		hide-padding
		on-click={onClick}
		on-focus={onFocus}
	/>
);

const view = (state, {dispatch, updateState}) => {
	const {
		items,
		selectedItem,
		hideNumbers,
		showCompletedCount
	} = state.properties;
	const {anchor, offset, focusedItem, lastFocusedItem, componentId} = state;
	const {width} = state.behaviors.resize.size;

	const stepsPerPage = getStepsPerPage(items, width);
	const isPaginated = items.length > stepsPerPage;
	const [rangeStart, rangeEnd] = getRange(
		anchor,
		offset,
		stepsPerPage,
		items.length
	);
	if (rangeStart < 0) {
		updateState({
			anchor: 'start',
			offset: 0
		});
	}
	if (rangeEnd > items.length) {
		updateState({
			anchor: 'end',
			offset: 0
		});
	}

	const steps = renderSteps({
		items,
		rangeStart,
		rangeEnd,
		selectedItem,
		focusedItem,
		lastFocusedItem,
		hideNumbers,
		isPaginated,
		dispatch,
		updateState,
		componentId
	});

	const arrowStartDisabled = rangeStart === 0;
	const arrowStart = renderArrow({
		icon: 'chevron-start-outline',
		a11yLabel: t('Previous {0} steps', stepsPerPage),
		disabled: arrowStartDisabled,
		onClick: () => {
			if (!arrowStartDisabled) {
				dispatch(() => {
					return {
						type: 'NOW_STEPPER#PREVIOUS_PAGE_BUTTON_CLICKED',
						payload: {
							stepsPerPage
						}
					};
				});
			}
		},
		onFocus: async () => {
			await Promise.resolve();
			updateState({focusedItem: LEFT_ARROW});
		}
	});

	const arrowEndDisabled = rangeEnd === items.length;
	const arrowEnd = renderArrow({
		icon: 'chevron-end-outline',
		a11yLabel: t('Next {0} steps', stepsPerPage),
		disabled: arrowEndDisabled,
		onClick: () => {
			if (!arrowEndDisabled) {
				dispatch(() => {
					return {
						type: 'NOW_STEPPER#NEXT_PAGE_BUTTON_CLICKED',
						payload: {
							stepsPerPage
						}
					};
				});
			}
		},
		onFocus: async () => {
			await Promise.resolve();
			updateState({focusedItem: RIGHT_ARROW});
		}
	});

	const onFocusOut = async (e) => {
		const host = getShadowRootHost(e.target);
		const relatedTargetHost = e.relatedTarget
			? getShadowRootHost(e.relatedTarget)
			: null;
		const nextFocusNotInStepper = relatedTargetHost !== host;
		if (nextFocusNotInStepper) {
			await Promise.resolve();
			updateState({focusedItem: null});
		}
	};

	return (
		<Fragment>
			<div
				className="now-stepper"
				style={{minWidth: minStepperWidth(items) + 'px'}}
				on-focusout={onFocusOut}>
				{isPaginated ? arrowStart : ''}
				<div role="tablist" className="now-stepper-steps">
					{steps}
				</div>
				{isPaginated ? arrowEnd : ''}
			</div>
			{showCompletedCount
				? renderCompletedCount(items.length, getCompletedCount(items))
				: null}
			<now-tooltip
				id="tooltip"
				manage-opened
				opened={state.currentTooltip.opened}
				target-ref={state.currentTooltip.ref}
				content={state.currentTooltip.content}
				delay={{show: 0, hide: 0}}
			/>
		</Fragment>
	);
};

// ACTION HANDLERS

const previousPageHandler = ({state, action, updateState}) => {
	const {items} = state.properties;
	let {anchor, offset} = state;
	const {stepsPerPage} = action.payload;
	const length = items.length;

	const nextOffset =
		anchor === 'start' ? offset - stepsPerPage : offset + stepsPerPage;
	const [rangeStart] = getRange(anchor, nextOffset, stepsPerPage, length);

	if (rangeStart <= 0) {
		// we overshot the beginning, so flip the anchor and reset the offset
		updateState((state) => {
			return {
				value: {
					anchor: 'start',
					offset: 0,
					focusedItem: state.properties.items[0],
					lastFocusedItem: null
				},
				operation: 'merge'
			};
		});
	} else {
		// show previous page
		updateState({
			offset: nextOffset,
			focusedItem: LEFT_ARROW,
			lastFocusedItem: null
		});
	}
};

const nextPageHandler = ({state, action, updateState}) => {
	const {items} = state.properties;
	const {anchor, offset} = state;
	const {stepsPerPage} = action.payload;
	const length = items.length;

	const nextOffset =
		anchor === 'start' ? offset + stepsPerPage : offset - stepsPerPage;
	const [, rangeEnd] = getRange(anchor, nextOffset, stepsPerPage, length);
	if (rangeEnd > length) {
		// we overshot the end so flip the anchor and reset the offset
		updateState((state) => {
			return {
				value: {
					anchor: 'end',
					offset: 0,
					focusedItem: state.properties.items[length - 1],
					lastFocusedItem: null
				},
				operation: 'merge'
			};
		});
	} else if (rangeEnd === length) {
		// we're on the last page
		updateState((state) => {
			return {
				value: {
					offset: nextOffset,
					focusedItem: state.properties.items[length - 1],
					lastFocusedItem: null
				},
				operation: 'merge'
			};
		});
	} else {
		// show next page
		updateState({
			offset: nextOffset,
			focusedItem: RIGHT_ARROW,
			lastFocusedItem: null
		});
	}
};

const previousStepHandler = ({state, updateState}) => {
	const {items} = state.properties;
	const {width} = state.behaviors.resize.size;
	const {focusedItem, anchor, offset} = state;
	// if the currently focused item is not a step, do nothing
	if (
		!focusedItem ||
		focusedItem === LEFT_ARROW ||
		focusedItem === RIGHT_ARROW
	) {
		return;
	}

	const lastFocusedItemIndex = items.findIndex((item) => item === focusedItem);
	const focusedItemIndex = lastFocusedItemIndex - 1;
	// if step to focus is outside bounds of items, do nothing
	if (focusedItemIndex < 0) {
		return;
	}

	const stepsPerPage = getStepsPerPage(items, width);
	const [rangeStart, rangeEnd] = getRange(
		anchor,
		offset,
		stepsPerPage,
		items.length
	);
	const itemInVisibleRange =
		rangeStart <= focusedItemIndex && focusedItemIndex < rangeEnd;
	const stateChanges = {};
	if (!itemInVisibleRange) {
		const nextOffset = anchor === 'start' ? offset - 1 : offset + 1;
		stateChanges.offset = nextOffset;
	}
	stateChanges.lastFocusedItem = items[focusedItemIndex];
	stateChanges.focusedItem = items[focusedItemIndex];
	updateState(stateChanges);
};

const nextStepHandler = ({state, updateState}) => {
	const {items} = state.properties;
	const {width} = state.behaviors.resize.size;
	const {focusedItem, anchor, offset} = state;
	// if the currently focused item is not a step, do nothing
	if (
		!focusedItem ||
		focusedItem === LEFT_ARROW ||
		focusedItem === RIGHT_ARROW
	) {
		return;
	}

	const lastFocusedItemIndex = items.findIndex((item) => item === focusedItem);
	const focusedItemIndex = lastFocusedItemIndex + 1;
	// if step to focus is outside bounds of items, do nothing
	if (focusedItemIndex === items.length) {
		return;
	}

	const stepsPerPage = getStepsPerPage(items, width);
	const [rangeStart, rangeEnd] = getRange(
		anchor,
		offset,
		stepsPerPage,
		items.length
	);
	const itemInVisibleRange = inRange(focusedItemIndex, rangeStart, rangeEnd);
	const stateChanges = {};
	if (!itemInVisibleRange) {
		const nextOffset = anchor === 'start' ? offset + 1 : offset - 1;
		stateChanges.offset = nextOffset;
	}
	stateChanges.lastFocusedItem = items[focusedItemIndex];
	stateChanges.focusedItem = items[focusedItemIndex];
	updateState(stateChanges);
};

const firstStepHandler = ({state, updateState}) => {
	const {focusedItem} = state;
	// if the currently focused item is not a step, do nothing
	if (
		!focusedItem ||
		focusedItem === LEFT_ARROW ||
		focusedItem === RIGHT_ARROW
	) {
		return;
	}

	updateState((state) => {
		return {
			value: {
				anchor: 'start',
				offset: 0,
				focusedItem: state.properties.items[0],
				lastFocusedItem: null
			},
			operation: 'merge'
		};
	});
};

const lastStepHandler = ({state, updateState}) => {
	const {items} = state.properties;
	const {focusedItem} = state;
	// if the currently focused item is not a step, do nothing
	if (
		!focusedItem ||
		focusedItem === LEFT_ARROW ||
		focusedItem === RIGHT_ARROW
	) {
		return;
	}

	updateState((state) => {
		return {
			value: {
				anchor: 'end',
				offset: 0,
				focusedItem: state.properties.items[items.length - 1],
				lastFocusedItem: null
			},
			operation: 'merge'
		};
	});
};

/**
 * Stepper is a tabular navigation that allows users to progress through a
 * process, know their current location, and switch to another step.
 *
 * ```jsx
 * const items = [
 *  {id: 'step1', label: 'Not started', progress: 'none'},
 *	{id: 'step2', label: 'Active', progress: 'partial'},
 *	{id: 'step3', label: 'In progress', progress: 'partial'},
 *	{id: 'step4', label: 'Complete', progress: 'done'}
 * ];
 *
 * <now-stepper items={items} selected-items="step2" show-completed-count />
 * ```
 *
 * @seismicElement now-stepper
 * @summary Tabular navigation component that helps users visualize and interact with a multi-step process.
 * @uib.label Stepper
 * @uib.icon stepper
 * @uib.description Tabular navigation component that helps users visualize and interact with a multi-step process.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties items, selectedItem, hideNumbers, showCompletedCount
 * @uib.actions NOW_STEPPER#SELECTED_ITEM_SET, NOW_STEPPER#NEXT_PAGE_BUTTON_CLICKED, NOW_STEPPER#PREVIOUS_PAGE_BUTTON_CLICKED
 * @uib.slots
 */
createEnhancedElement('now-stepper', {
	properties: {
		/**
		 * An array of item objects describing each step in the stepper.
		 * Each object must have a unique id.
		 * Items with `disabled` are visible but cannot be selected by the user.
		 * @type {{id: (string|number), label: string, icon: string, sublabel: string, progress: ("none"|"partial"|"done"), disabled:boolean}[]}
		 * @uib.description An array of items each describing a step
		 * @uib.defaultValue [{"id": "step1","label": "Not started","progress": "none"},{"id": "step2","label": "Active","progress": "partial"},{"id": "step3","label": "In progress","progress": "partial"},{"id": "step4","label": "Complete","progress": "done","disabled": true}]
		 * @uib.typeMetadata.emptyStateBehavior EMPTY_ARRAY
		 */
		items: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						id: {type: ['string', 'number']},
						label: {type: 'string', translatable: true},
						icon: {type: 'string'},
						sublabel: {type: 'string', translatable: true},
						progress: {type: 'string', enum: ['none', 'partial', 'done']},
						disabled: {type: 'boolean'}
					}
				}
			}
		},
		/**
		 * The id of the current step.
		 * @type {(string|number)}
		 * @uib.fieldType string
		 * @uib.description The id of the current step
		 */
		selectedItem: {manageable: true, schema: {type: ['string', 'number']}},
		/**
		 * When true, hides numbers on each step in the stepper.
		 * @type {boolean}
		 * @uib.description Hides numbers on each step in the stepper
		 */
		hideNumbers: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, displays a count under the stepper noting the number of
		 * completed steps and the number of total steps.
		 * @type {boolean}
		 * @uib.description Displays the current step number and the total number of steps
		 */
		showCompletedCount: {default: false, schema: {type: 'boolean'}}
	},
	initialState: {
		/**
		 * Whether we are slicing items from the beginning or end of the list
		 * @type {('start'||'end')}
		 */
		anchor: 'start',
		/**
		 * Keeps track of far are we from the anchor
		 * @type {number}
		 */
		offset: 0,
		/**
		 * Keeps track of the currently focused item (step or arrow)
		 * @type {(null|Step)}
		 */
		focusedItem: null,
		/**
		 * Keeps track of the last focused step
		 * @type {(null|Step)}
		 */
		lastFocusedItem: null,
		currentTooltip: {ref: null, content: '', opened: false}
	},
	view,
	eventHandlers: [
		{
			events: ['keydown'],
			effect: ({action, dispatch}) => {
				const {key} = action.payload.event;
				if (['ArrowLeft', 'ArrowRight', 'Home', 'End'].includes(key)) {
					action.preventDefault();
				}
				if (key === 'ArrowLeft') {
					dispatch(() => {
						return {
							type: 'FOCUS_PREVIOUS_STEP',
							payload: {}
						};
					});
				} else if (key === 'ArrowRight') {
					dispatch(() => {
						return {
							type: 'FOCUS_NEXT_STEP',
							payload: {}
						};
					});
				} else if (key === 'Home') {
					dispatch(() => {
						return {
							type: 'FOCUS_FIRST_STEP',
							payload: {}
						};
					});
				} else if (key === 'End') {
					dispatch(() => {
						return {
							type: 'FOCUS_LAST_STEP',
							payload: {}
						};
					});
				}
			}
		}
	],
	behaviors: [
		{behavior: focusBehavior},
		{
			behavior: resizeBehavior,
			options: {timer: {type: 'throttle', wait: 30}}
		}
	],
	styles,
	actionHandlers: {
		...createTooltipActionHandler(hideTooltip),
		'NOW_STEPPER#PREVIOUS_PAGE_BUTTON_CLICKED': previousPageHandler,
		'NOW_STEPPER#NEXT_PAGE_BUTTON_CLICKED': nextPageHandler,
		FOCUS_PREVIOUS_STEP: {
			stopPropagation: true,
			effect: previousStepHandler
		},
		FOCUS_NEXT_STEP: {
			stopPropagation: true,
			effect: nextStepHandler
		},
		FOCUS_FIRST_STEP: {
			stopPropagation: true,
			effect: firstStepHandler
		},
		FOCUS_LAST_STEP: {
			stopPropagation: true,
			effect: lastStepHandler
		},
		[COMPONENT_RENDERED]: ({state, action}) => {
			const {host} = action.payload;
			const {focusedItem} = state;
			const activeElement = getActiveElement(action.payload.host);
			if (
				focusedItem &&
				focusedItem.id &&
				activeElement.getAttribute('data-itemid') !== focusedItem.id
			) {
				host.shadowRoot
					.querySelector(`[data-itemid="${focusedItem.id}"]`)
					.focus();
			}
		},
		[COMPONENT_DISCONNECTED]: ({action}) => {
			const componentId = action.meta.id;
			unschedule(componentId);
		}
	},
	dispatches: {
		/**
		 * Dispatched when the stepper is paginated and the user clicks the
		 * "previous page" button to see previous steps that are not visible in
		 * the current view.
		 * @type {{stepsPerPage: number}}
		 * @uib.description Dispatched when the stepper is paginated and the user clicks the "previous page" button to see previous steps that are not visible in the current view
		 */
		'NOW_STEPPER#PREVIOUS_PAGE_BUTTON_CLICKED': {
			schema: {
				type: 'object',
				properties: {
					stepsPerPage: {type: 'number'}
				}
			}
		},
		/**
		 * Dispatched when the stepper is paginated and the user clicks the
		 * "next page" button to see additional steps that are not visible in
		 * the current view.
		 * @type {{stepsPerPage: number}}
		 * @uib.description Dispatched when the stepper is paginated and the user clicks the "next page" button to see additional steps that are not visible in the current view
		 */
		'NOW_STEPPER#NEXT_PAGE_BUTTON_CLICKED': {
			schema: {
				type: 'object',
				properties: {
					stepsPerPage: {type: 'number'}
				}
			}
		},
		/**
		 * Dispatched when a step is selected.
		 * Payload key `value` contains the `id` of the item selected.
		 * Payload key `item` contains the `object` of the the item selected.
		 * Set the `manage-selected-item` property to override the
		 * default behavior and handle this action manually.
		 * @type {{value: (string|number), item: Object}}
		 * @uib.description Dispatched when a step is selected
		 */
		'NOW_STEPPER#SELECTED_ITEM_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: ['string', 'number']},
					item: {type: 'object'}
				}
			}
		}
	}
});
